class index extends Component {
  render() {
    return (
      <Layout>
        <Header />
      </Layout>
    );
  }
}

export default connect(
  state => ({
    visible: state.base.visible,
  }),
  dispatch => ({
    hideSidebar: () => dispatch(baseActions.hideSidebar()),
    showSidebar: () => dispatch(baseActions.showSidebar()),
  }),
)(index);
