import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Link } from 'gatsby';
import onClickOutside from 'react-onclickoutside';
import './style.scss';
import * as postActions from '../../state/modules/post';
import * as baseActions from '../../state/modules/base';
import { connect } from 'react-redux';

class Sidebar extends Component {
  constructor(props) {
    super(props);

    this.handleClickOutside = this.handleClickOutside.bind(
      this,
    );
  }
  handleClickOutside = evt => {
    evt.stopPropagation();
    console.log('evt,', evt);
    console.log('props,', this.props);
    this.props.visible
      ? this.props.hideSidebar()
      : this.props.showSidebar();
  };
  goLink() {
    console.log('1241452');
    // this.props.history.push('/about');
  }
  render() {
    const linkElement = <Link to="/about">About</Link>;
    return (
      <div className="sidebar">
        <div className="sidebar__inner">
          {/* {linkElement} */}
          <span onClick={this.goLink}>about</span>
          {/* <Link to="/about">About</Link> */}
        </div>
        <div className="sidebar__inner">
          <Link to="/posts">Posts</Link>
        </div>
        <div className="sidebar__inner">
          <Link to="/tags">Tags</Link>
        </div>
      </div>
    );
  }
}

Sidebar = onClickOutside(Sidebar);

Sidebar = connect(
  state => ({
    number: state.post.number,
    visible: state.base.visible,
  }),
  dispatch => ({
    hideSidebar: () => dispatch(baseActions.hideSidebar()),
    showSidebar: () => dispatch(baseActions.showSidebar()),
  }),
)(Sidebar);

export default Sidebar;
