import React, { Component, Fragment } from 'react';
import { Link } from 'gatsby';
import Hamburger from '../Hamburger';
import Sidebar from '../Sidebar';
import './style.scss';
import * as postActions from '../../state/modules/post';
import * as baseActions from '../../state/modules/base';
import { connect } from 'react-redux';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isActive: false,
      visible: false,
    };
    this.dbc = this.dbc.bind(this);
  }
  dbc() {
    console.log('dbc props', this.props);
    this.props.visible
      ? this.props.hideSidebar()
      : this.props.showSidebar();
  }
  render() {
    console.log(this.state);
    return (
      <Fragment>
        <div className="header">
          <div className="header__title">
            <Link to="/">SangJeong</Link>
          </div>
          <div className="header__menu">
            <Hamburger
              onOpen={this.dbc}
              active={this.props.visible}
            />
          </div>
          <div className="header__sub-title">
            <Link to="/about">About</Link>
            <Link to="/about">Posts</Link>
            <Link to="/about">Tags</Link>
          </div>
          {/* <div className="header__inner">
          <Link to="/about">About</Link>
        </div> */}
          {/* <div className="header__sub-title">
          <Link to="/articles">Posts</Link>
        </div>
        <div className="header__sub-title">
          <Link to="/tags">Tags</Link>
        </div> */}
        </div>
        {/* this.props.isClosed && */}
        {/* disableOnClickOutside={true}  */}
        {/* stopPropagation={true} */}
        {this.props.visible && (
          <Sidebar
            outsideClickIgnoreClass={'header__menu'}
          />
        )}
      </Fragment>
    );
  }
}

export default connect(
  state => ({
    number: state.post.number,
    visible: state.base.visible,
  }),
  dispatch => ({
    hideSidebar: () => dispatch(baseActions.hideSidebar()),
    showSidebar: () => dispatch(baseActions.showSidebar()),
  }),
)(Header);
